.bottom-navbar {
    bottom: 0;
    width: 80%;
    margin-left: 10%;
    background-color: #888787; /* You can change the background color */
    text-align: center;
    border-radius: 10px;
    margin-top: 2.5em;
   
  }
  
  .navbar-list {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: space-around; /* Adjust this to control spacing between links */
  }
  
  .navbar-item {
    margin: 0;
  }
  
  .navbar-item a {
    text-decoration: none;
    color: white; /* You can change the link color */
    padding: 10px 20px; /* Adjust padding as needed */
    display: inline-block;
    transition: background-color 0.3s;
    font-size: larger;
  }
  
  .navbar-item a:hover {
    background-color: #555; /* Change the background color on hover */
  }
  
  /* navbar.css */
.active-link {
    color: #FF0000; /* Change the color to your desired active link color */
    font-weight: bold; /* Optionally, make the active link text bold */
    /* Add other styling as needed */
  }
  